import ApiService from '../Api/getApi';
import connect from 'react-redux';
import {fetchCategoriesSuccess} from '../redux/action';
const apiCate = new ApiService();
function getApiCategoriesToShow() {
    return async function (dispatch) {
        const categories = await apiCate.get('categories');
        return dispatch(fetchCategoriesSuccess(categories))
    }
}
