import React, {Component} from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import '../../stylesheets/style.scss';
import logo from '../../ecolife.jpg';
import Freshfood from '../Product/Freshfood';
import Freshvegetables from '../Product/Freshvegetables';
import Pricesdrop from '../Product/Pricesdrop';
import Contactus from '../Product/Contactus';
const arrayNav =[
    {   
        id: 1,
        title: 'Home',
        url: '/'
    },
    {   
        id: 2,
        title: 'Fresh Food',
        url: '/fresh-food'
    },
    {   
        id: 3,
        title: 'Fresh Vegetables',
        url: '/fresh-vegetables',
        icon:'',
        subArray:
        [
            {   
                id: 1,
                subTitle: 'Potatoes',
                url: '/potatoes'
            },
            {   
                id: 2,
                subTitle: 'Onions',
                url: '/onions'
            },
            {   
                id: 3,
                subTitle: 'Carrots',
                url: '/carrots'
            },
            {   
                id: 4,
                subTitle: 'Broccoli',
                url: '/broccoli'
            },
            {   
                id: 5,
                subTitle: 'Cabbages',
                url: '/cabbages'
            }
        ]
    },
    {   
        id: 4,
        title: 'Prices Drop',
        url: '/prices-drop'
    },
    {   
        id: 5,
        title: 'Contact Us',
        url: '/contact-us'
    },
];
class Menu extends Component {
    constructor(props) {
        super(props)
        this.state = {
            isToggle : false,
        }
    }

    componentDidMount() {
        console.log("Render first.")
    }

    handleMouseEnter = () => {
        this.setState({isToggle:true});
    }
    handleMouseLeave = () => {
        this.setState({isToggle:false});
        // console.log("Toogle: ", this.state.isToggle);
    }
    render() {
        
        const {isToggle} = this.state;

        console.log("Toogle RENDER: ", isToggle);

        return(
            <Router>
                <div className= "nav">
                    <ul className="list">
                        {   
                            arrayNav.map((item,index) => (
                                <>
                                <li className= "item" key = {index}><Link to={`${item.url}`}>{item.title}{item.icon&&(<i className="icon ion-ios-arrow-down">{item.icon}</i>)}</Link>
                                <ul className="list-sub-item">
                                    {   item.subArray && (
                                        item.subArray.map((subItem,index) => (
                                            <li className="sub-item" key={index}><a href="#">{subItem.subTitle}</a></li>
                                        ))
                                    )
                                    }
                                </ul>
                                </li>
                                </>
                            ))
                        }
                    </ul>
                    <Switch>
                        <Route path="/fresh-food">
                            <Freshfood/>
                        </Route>
                        <Route path="/prices-drop">
                            <Pricesdrop/>
                        </Route>
                        <Route path="/contact-us">
                            <Contactus/>
                        </Route>
                        <Route path="/:id" children={<Freshvegetables/>}>
                        </Route>
                    </Switch>
                </div>
            </Router>
        )
    }
}
const Header = () => {
    return(
        <>
        <div className="header" id="header">
                <div className="wrapper_menu">
                    <h1  className="logo"><a><img src= {logo}/></a></h1>
                    <Menu/>
                </div>
        </div>
        </>
    )
}
const updateHeader= () => {
    window.addEventListener('scroll',() => {
        const header = document.getElementById('header');
        if (window.pageYOffset >= 10) {
            header.classList.add('active');
        } else {
            header.classList.remove('active');
        }
    });
}
updateHeader();
export default Header;
