import React,{Component} from 'react';
export const withLoading = ((WrapperComponent,fetchData) => {
    return class HigherComponent extends Component {
        constructor(props) {
            super(props)
            this.state = {
                    data : [],
                    isLoading: true
            }
        }
        async componentDidMount() {
            const data = await fetchData();
            return this.setState({
                data: data,
                isLoading: !this.state.isLoading
            });
        }
    
        componentDidUpdate() {
            // console.log('update');
        }
    
        render() {
            if (this.state.isLoading) {
                return <p>Loading ...</p>
            } else if (this.state.data === 0) {
                return <p>Data Empty...</p>
            } else {
            return(
                <WrapperComponent products = {this.state.data} {...this.props}/>
            )
            }
        }
    }
});
