import axios from 'axios';
const API_DOMAIN = 'https://604b401bee7cb900176a1a9e.mockapi.io';
export class ApiService {
    constructor() {
        axios.defaults.baseURL = `${API_DOMAIN}/api/v1/`;
    }
    get(url) {
        return new Promise((resolve,reject) => {
            axios.get(url)
            .then((res)=> {
                resolve(res.data)
            })
            .catch((error) => {
                reject(error);
            })
        })
    }
}
export default ApiService;
