import React,{Component} from 'react';
import axios from 'axios';
import {ApiService} from '../Api/getApi';
import numberFormat from '../Format/format';
import {store} from '../../App';
import {generalRatingStar} from './RatingStar'
import { withLoading } from '../redux/Hocs';
const api = new ApiService();

const fetchProduct = {
	getProducts: async () => {
		const responseProduct = await api.get('products');
		return responseProduct;
	}
}
const fetchCategories = {
	getCategories: () => {
		const responseCate = store.getState();
		return responseCate;
	}
}
class Product extends Component {
	render() {
		const categories = fetchCategories.getCategories().categories.categories;
		// console.log('categories in product', fetchCategories.getCategories().categories.categories);
		return(
				<div className="product">
					{
						this.props.products && (
							<>
							<List data={this.props.products} categories = {categories}/>
							</>
						)
					}
				</div>
		)
	}
}
const List = ({data,categories}) => {
	return(
		<ul className="list-product">
		{data.map((products, index) => (
		<li className="list-product-item" key={index}>
				<div className="wrapper-contents">
					<figure className="left text-center"><img className="item-img" src={products.cover}/></figure>
					<div className="right">
						{
							categories.map((cate)=> (
								cate.key === products.type && (<h3 className="categories">{cate.name}</h3>)
							))
						}
						<span className="title text-left">{products.name}</span>
						{
							+products.sales == 0 ? (
								<span className="price">{numberFormat(+products.price)}</span>
							) :
							<>
							<span className="price cancel">{numberFormat(+products.price)}</span>
							<span className="sales">
							{
								+products.sales != 0 &&
								(percentCalculation(products.sales, +products.price))
							}
							</span>
							</>
						}
						{	
							products.stars && 
							(
								<fieldset class="rating">
									{generalRatingStar(products.stars)}
								</fieldset>
							)
						}
						
					</div>
				</div>
				<button onClick= {() => {addItem(products.id)}}>Add to cart</button>
		</li>
		))}
		</ul>
	)
};
const percentCalculation = (sales,price) => {
	if (sales.indexOf('%') == -1)  {
		const cal = +sales + price;
		return numberFormat(cal);
	}else {
		const rlTotal = sales.replace(/[-/\\^$*+?.()|[\]{}%]/g,'');
		return numberFormat((price - (price*rlTotal)/100));
	}
}
const addItem = (id) => {
	console.log(id);
}
const HocComponent = withLoading(Product, (props) => fetchProduct.getProducts() )
export default HocComponent;
