export const generalRatingStar = (stars) => {
    const styleColors = {
		active: '#ffb25d',
		default: '#ffffff'
	}
    const limit = 5;
    const array = [];
    for (let i = 1; i <= limit; i++) {
        array.push(i);
    }
    return array.map(item => (
        <label key={item} class = "full" for={`activestar${item}`}	style={{color: item <= stars ? styleColors.active : styleColors.default}}></label> 
    ))	
}
